/* src/App.css */
body {
  margin: 0;
  font-family: "Arial", sans-serif;
  background-color: #0a0a0a;
  color: white;
  text-align: center;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.hero {
  width: 100%;
  padding: 100px 20px;
  background: linear-gradient(135deg, #1a1a1a, #282828);
}

.hero h1 {
  font-size: 48px;
  margin: 0;
}

.hero p {
  font-size: 20px;
  margin: 10px 0 20px;
}

.cta-button {
  display: inline-block;
  padding: 10px 25px;
  font-size: 18px;
  color: white;
  background-color: #ff9900;
  text-decoration: none;
  border-radius: 5px;
  transition: background 0.3s ease-in-out;
}

.cta-button:hover {
  background-color: #e68a00;
}

.about {
  padding: 50px 20px;
  max-width: 800px;
}

.footer {
  padding: 20px;
  background-color: #060606;
  width: 100%;
}
